.mm-navbar {
  display: flex;
  align-items: center;
  height: 90px;
  padding: 0 10%;
  box-shadow: 0 2pt 12pt #e9e6e6; }
  .mm-navbar img {
    width: 135.7px;
    height: 30px;
    padding: 8px 12px;
    margin-right: 50px; }
  .mm-navbar a {
    color: #551a8b;
    margin-right: 30px;
    text-decoration: none;
    font-weight: bold;
    text-transform: capitalize; }
  .mm-navbar .active {
    position: relative; }
    .mm-navbar .active::after {
      content: "";
      display: block;
      position: absolute;
      bottom: -10px;
      width: 120%;
      transform: translateX(-10%);
      border-bottom: 3px solid #e60000; }
